import { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';

// material-ui

// project imports
import MainCard from 'ui-component/cards/MainCard';
import { Languages } from './Languages';
import OrderTable from './component/OrderTable';
import OrderFilter from './component/OrderFilter';

// ==============================|| SINGLE PAGE APPLICATION ||============================== //

let fundArr = []; //ËµÑÈáë‰ø°ÊÅØÔºåÊöÇÂ≠ò
const App = () => {
    console.log('order app running');
    let navigate = useNavigate(); //react-router-dom
    const browserLocale = /zh/i.test(navigator.language) ? 'zhCN' : 'enUS';
    const globalization = useSelector((state) => state.globalization);
    const [loginData] = useState(globalization.baseData.loginData);
    const localization = useSelector((state) => state.localization); //read only (language related)
    let [langPack, setLangPack] = useState(Languages[localization.locale]); //currentLanguage

    let [page, setPage] = useState(0);
    let [page_size, setPage_size] = useState(10);
    let [total, setTotal] = useState(0);

    //data grid Áõ∏ÂÖ≥
    let [tableColumns, setTableColumns] = useState([]);
    let [tableRows, setTableRows] = useState([]);
    let [columnVisibilityModel, setColumnVisibilityModel] = useState({});
    const [fundAutoQuery, setFundAutoQuery] = useState(false);

    //filter Áõ∏ÂÖ≥
    const [startDate, setStartDate] = useState(null);
    const [endDate, setEndDate] = useState(null);
    const [dateType, setDateType] = useState('dateCreated');
    const [buyerId, setBuyerId] = useState('');

    const setDate = (value, name) => {
        const dateCheck = (dateEarly, dateLate) => {
            if (dateEarly.valueOf() >= dateLate.valueOf()) return queryResult(false, 'date error');
            return true;
        };
        if (startDate == null || endDate == null) {
            if (name === 'startDate') {
                setStartDate(value);
            } else if (name === 'endDate') {
                setEndDate(value);
            }
        } else {
            if (name === 'startDate') {
                if (dateCheck(value, endDate)) setStartDate(value);
            } else if (name === 'endDate') {
                if (dateCheck(startDate, value)) setEndDate(value);
            }
        }
        return;
    };

    useEffect(() => {
        setLangPack(Languages[localization.locale || browserLocale]);
        return () => {};
    }, [localization.locale]); //ËÆ¢ÈòÖËØ≠Ë®ÄÂèòÂåñ

    useEffect(() => {
        if (startDate || endDate) {
            autoQuery();
        }
        return () => {};
    }, [startDate, endDate, dateType]); //ËÆ¢ÈòÖËØ≠Ë®ÄÂèòÂåñ

    useEffect(() => {
        if (tableRows.length && fundAutoQuery) {
            queryFund();
        } else {
            tableFormatter();
        }
        return () => {};
    }, [tableRows, fundAutoQuery]); //ËÆ¢ÈòÖË°®Ê†ºÂèòÂåñ

    useEffect(() => {
        console.log('I am at order');
        if (!loginData) return navigate('/');
        autoQuery();
        tableFormatter();
        return () => {
            console.log('I leave order');
        };
    }, []);

    //____________‰∏Ä‰∫õÂáΩÊï∞_______________________________________________

    const queryDateConvert = (value) => {
        let PT_GMT_HOUR_GAP = new Date().isPDT() ? -7 : -8; //Â§™Âπ≥Ê¥ãÊó∂Èó¥‰∏éÊ†ºÊûóÂ®ÅÊ≤ªÊó∂Èó¥ÁöÑÊó∂Â∑Æ
        let timeDifference = PT_GMT_HOUR_GAP * 60 * 60 * 1000;
        let localTimeZero = new Date(new Date(value).Format('yyyy-MM-dd 00:00:00')).valueOf();
        let gmtZero = localTimeZero + new Date().getTimezoneOffset() * 60 * 1000; //GMT 00:00:00
        let ptZero = gmtZero + timeDifference; //PT 00:00:00
        return ptZero; //[Number]
    }; //ÂΩìÂú∞Êó∂Èó¥0ÁÇπ üëâ Â§™Âπ≥Ê¥ãÊó∂Èó¥0ÁÇπ

    function autoQuery(obj) {
        let paramObj = {
            role: 'seller',
            start_page: page,
            page_size: page_size
        };
        if (startDate || endDate) {
            if (dateType === 'dateCreated') {
                if (startDate) {
                    paramObj.create_date_start = {
                        time: queryDateConvert(startDate)
                    };
                }
                if (endDate) {
                    paramObj.create_date_end = {
                        time: queryDateConvert(endDate)
                    };
                }
            } else if (dateType === 'dateModified') {
                if (startDate) {
                    paramObj.modified_date_start = {
                        time: queryDateConvert(startDate)
                    };
                }
                if (endDate) {
                    paramObj.modified_date_end = {
                        time: queryDateConvert(endDate)
                    };
                }
            }
        }
        if (buyerId) paramObj.other_login_id = buyerId;
        if (obj) Object.assign(paramObj, obj);
        return queryOrder({
            param_trade_ecology_order_list_query: JSON.stringify(paramObj)
        });
    }

    async function queryOrder(obj) {
        let paramObj = ma.sellerOrderQuery(obj);
        let msg = await fetchAPI(paramObj);
        if (!msg.response.status) return queryResult(false, msg.response.result);
        let res = ma.sellerOrderCheck(msg);
        let { status, result } = res.response;
        console.log(result);
        if (!status) {
            setTotal(0);
            setTableRows([]);
            if (status === 0) return queryResult(false, langPack.form.orderError);
            return queryResult(status, result);
        }
        let { order_list, total_count } = result;
        setTotal(total_count);
        let row = order_list.trade_ecology_order.map((obj) => {
            return {
                ...obj,
                id: obj.trade_id
            };
        });
        return setTableRows(row);
    }

    async function queryFund() {
        if (!tableRows.length) return;
        fundArr = [];
        fundArr.realLength = 0;
        tableRows.forEach((obj) => {
            console.log('checking funds');
            let { trade_id } = obj;
            queryOrderFund({ e_trade_id: trade_id });
        });
        return;
    } //ËÆ¢ÂçïËµÑÈáë`ÊâπÈáèÊü•ËØ¢

    async function queryOrderFund(obj) {
        let paramObj = ma.sellerOrderFundQuery(obj);
        let msg = await fetchAPI(paramObj);
        if (!msg.response.status) return queryResult(false, msg.response.result);
        let res = ma.sellerOrderFundCheck(msg);
        let { status, result } = res.response;
        let { e_trade_id } = res.request.data;
        let index = tableRows.findIndex((obj) => obj.trade_id === e_trade_id);
        if (index === -1) {
            fundArr = [];
            return;
        } //Ê≤°ÊúâÊâæÂà∞ÂØπÂ∫îÁöÑËÆ¢ÂçïÔºåÂèØËÉΩÂà∑Êñ∞‰∫Ü
        if (!status) {
            fundArr[index] = {};
            queryResult(false, langPack.form.orderFundError);
        } else fundArr[index] = result;
        fundArr.realLength++;
        if (fundArr.realLength !== tableRows.length) return; //ËøòÊú™ÈõÜÈΩê
        fundArr = fundArr.map((obj) => {
            let { service_fee, fund_pay_list } = obj;
            if (service_fee) {
                let { amount, currency } = service_fee;
                obj.serviceAmount = amount;
                obj.serviceCurrency = currency;
            }
            if (fund_pay_list) {
                let { pay_amount, pay_time, receive_time, should_pay_amount, receive_amount } = fund_pay_list.fund_pay[0];
                Object.assign(obj, fund_pay_list.fund_pay[0]);
                if (should_pay_amount) {
                    obj.shouldPayCurrency = should_pay_amount.currency;
                    obj.shouldPayAmount = should_pay_amount.amount;
                }
                if (pay_amount) {
                    obj.payCurrency = pay_amount.currency;
                    obj.payAmount = pay_amount.amount;
                }
                if (receive_amount) {
                    obj.receiveCurrency = receive_amount.currency;
                    obj.receiveAmount = receive_amount.amount;
                }
                if (pay_time) {
                    obj.payTime = pay_time.timestamp;
                }
                if (receive_time) {
                    obj.receiveTime = receive_time.timestamp;
                }
            }
            return obj;
        });
        setTableRows((prev) => {
            let newArr = [];
            for (let i = 0; i < prev.length; i++) {
                newArr.push({ ...prev[i], ...fundArr[i] });
            }
            console.log(newArr);
            return newArr;
        });
        fundArr = [];
        tableFormatter({ fund: true });
    } //ËÆ¢ÂçïËµÑÈáëÊü•ËØ¢

    //____________ ProductTable functions ___________________________________________________
    function tableFormatter(obj) {
        const columns = [
            {
                field: 'trade_id',
                headerName: langPack.table.tableHeader,
                flex: 1
            },
            {
                field: 'timeOrderCreated',
                flex: 1,
                valueGetter: ({ row }) => row,
                valueFormatter: ({ value }) => new Date(value.timeOrderCreated).Format('yyyy-MM-dd hh:mm'),
                headerName: langPack.table.timeCreated
            },
            {
                field: 'timeOrderModified',
                flex: 1,
                valueGetter: ({ row }) => row,
                valueFormatter: ({ value }) => new Date(value.timeOrderModified).Format('yyyy-MM-dd hh:mm'),
                headerName: langPack.table.timeModified
            }
        ];
        if (obj) {
            let { fund } = obj;
            if (fund) {
                columns.push(
                    {
                        field: 'pay_status',
                        headerName: langPack.table.pay_status,
                        flex: 1,
                        sortable: true
                    },
                    {
                        field: 'receiveAmount',
                        headerName: langPack.table.receiveAmount,
                        flex: 1,
                        sortable: true
                    },
                    {
                        field: 'payAmount',
                        headerName: langPack.table.payAmount,
                        flex: 1,
                        sortable: true
                    },
                    {
                        field: 'shouldPayAmount',
                        headerName: langPack.table.shouldPayAmount,
                        flex: 1,
                        sortable: true
                    },
                    {
                        field: 'pay_step',
                        headerName: langPack.table.pay_step,
                        flex: 1,
                        sortable: true
                    },
                    {
                        field: 'pay_method',
                        headerName: langPack.table.pay_method,
                        flex: 1,
                        sortable: true
                    },
                    {
                        field: 'serviceAmount',
                        headerName: langPack.table.serviceAmount,
                        flex: 1,
                        sortable: true
                    },
                    {
                        field: 'shouldPayCurrency',
                        headerName: langPack.table.currency,
                        flex: 0.5,
                        sortable: true
                    },
                    {
                        field: 'receiveTime',
                        valueGetter: ({ row }) => row,
                        valueFormatter: ({ value }) => (value.receiveTime ? new Date(value.receiveTime).Format('yyyy-MM-dd hh:mm') : ''),
                        headerName: langPack.table.receiveTime,
                        flex: 1,
                        sortable: true
                    },
                    {
                        field: 'payTime',
                        valueGetter: ({ row }) => row,
                        valueFormatter: ({ value }) => (value.payTime ? new Date(value.payTime).Format('yyyy-MM-dd hh:mm') : ''),
                        headerName: langPack.table.payTime,
                        flex: 1,
                        sortable: true
                    }
                ); // insert
            }
        }
        setTableColumns(columns);
        setColumnVisibilityModel({
            pay_step: false, //ÊîØ‰ªòÊ≠•È™§ÔºåÂ§ßÂ§öÈáçÂ§ç
            pay_method: false //ÊîØ‰ªòÊñπÂºèÔºåËÉΩ‰ªòÂ∞±Ë°å
        });
    }

    const handleChangePage = (newPage) => {
        setPage(newPage);
        tableFormatter();
        autoQuery({ start_page: newPage });
    }; //ÁøªÈ°µ
    const handleChangeRowsPerPage = (newSize) => {
        setPage_size(newSize);
        tableFormatter();
        autoQuery({ page_size: newSize });
    }; //È°µÈù¢Â§ßÂ∞èË∞ÉÊï¥
    const onRowClick = (obj, e) => {
        // let { id, row } = obj;
    }; //ÂçïÂÖÉÊ†ºÁÇπÂáª

    //____________JSX ÂáΩÊï∞_______________________________________________

    return (
        <MainCard title="Order --ËÆ¢Âçï">
            <OrderFilter
                localization={localization}
                langPack={langPack}
                startDate={startDate}
                endDate={endDate}
                setDate={setDate}
                setDateType={setDateType}
                autoQuery={autoQuery}
                buyerId={buyerId}
                setBuyerId={setBuyerId}
            />
            <OrderTable
                tableRows={tableRows}
                tableColumns={tableColumns}
                page_size={page_size}
                page={page}
                total={total}
                localization={localization}
                langPack={langPack}
                columnVisibilityModel={columnVisibilityModel}
                setColumnVisibilityModel={setColumnVisibilityModel}
                handleChangePage={handleChangePage}
                handleChangeRowsPerPage={handleChangeRowsPerPage}
                fundAutoQuery={fundAutoQuery}
                setFundAutoQuery={setFundAutoQuery}
                onRowClick={onRowClick}
            />
        </MainCard>
    );
};

export default App;
