import { useState } from 'react';
import PropTypes from 'prop-types';

// material-ui
import { TextField, Box, Slider, Tooltip, Button, Typography } from '@mui/material';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { DesktopDatePicker } from '@mui/x-date-pickers/DesktopDatePicker';

//date-fns
import cnLocale from 'date-fns/locale/zh-CN';
import enLocale from 'date-fns/locale/en-GB';

const localeMap = {
    zhCN: cnLocale,
    enUS: enLocale
};

const maskMap = {
    zhCN: '__-__-__',
    enUS: '__/__/____'
};

function App({ localization, langPack, startDate, endDate, setDate, setDateType, autoQuery, buyerId, setBuyerId }) {
    let { locale } = localization;
    let dateMasks = [
        {
            value: 0,
            label: langPack.table.timeCreated
        },
        {
            value: 100,
            label: langPack.table.timeModified
        }
    ];
    return (
        <div style={{ marginBottom: '7px', display: 'flex', flexDirection: 'row' }}>
            <Tooltip title={langPack.form.buyerId} key="buyerId">
                <TextField
                    margin="normal"
                    id={buyerId}
                    variant="outlined"
                    sx={{ margin: '0px' }}
                    label={
                        <Typography variant="h3" sx={{ color: 'rgb(133, 133, 133)', fontWeight: 'normal' }}>
                            {langPack.form.buyerId}
                        </Typography>
                    }
                    defaultValue={buyerId}
                    onBlur={(e) => setBuyerId(e.target.value)}
                />
            </Tooltip>
            <LocalizationProvider dateAdapter={AdapterDateFns} locale={localeMap[locale]}>
                <DesktopDatePicker
                    label={langPack.form.startDate}
                    mask={maskMap[locale]}
                    value={startDate}
                    onChange={(value) => setDate(value, 'startDate')}
                    renderInput={(params) => (
                        <Tooltip title={langPack.form.dateConversion}>
                            <TextField {...params} />
                        </Tooltip>
                    )}
                />
                <DesktopDatePicker
                    label={langPack.form.endDate}
                    mask={maskMap[locale]}
                    value={endDate}
                    onChange={(value) => setDate(value, 'endDate')}
                    renderInput={(params) => (
                        <Tooltip title={langPack.form.dateConversion}>
                            <TextField {...params} />
                        </Tooltip>
                    )}
                />
            </LocalizationProvider>
            <Box sx={{ marginLeft: '50px', marginRight: '50px', width: 100 }}>
                <Slider
                    defaultValue={0}
                    step={100}
                    marks={dateMasks}
                    onChangeCommitted={(e, v) => setDateType(v === 0 ? 'dateCreated' : 'dateModified')}
                />
            </Box>
            <Button onClick={() => autoQuery()} variant="outlined">
                {langPack.form.query}
            </Button>
        </div>
    );
}

App.propTypes = {
    localization: PropTypes.object,
    langPack: PropTypes.object,
    startDate: PropTypes.object,
    endDate: PropTypes.object,
    setDate: PropTypes.func,
    setDateType: PropTypes.func,
    autoQuery: PropTypes.func,
    buyerId: PropTypes.string,
    setBuyerId: PropTypes.func
};

export default App;
